const functions = {
  hasErrors: fieldsError => {
    return Object.keys(fieldsError).some(field => fieldsError[field]);
  },
  getBase64: file => {
    return new Promise((resolve, reject) => {
      let reader = new FileReader();
      reader.readAsDataURL(file);
      let tmp = {
        fileName: file.name,
        fileType: file.type,
      };
      reader.onload = function() {
        tmp['file'] = reader.result;
        resolve(tmp);
      };

      reader.onerror = function(error) {
        reject(error);
      };
    });
  },
  checkFile: (file, allowedFiles) => {
    var extension = file.name.substr(file.name.lastIndexOf('.') + 1);
    var regEx = new RegExp(allowedFiles, 'gi');
    if (!regEx.test(extension)) {
      return false;
    }
    return true;
  },
  getStatus: status => {
    status = parseInt(status);

    if (status < 0) {
      return 'Processing Error';
    }
    if (status === 3) {
      return 'Waiting';
    }
    if (status === 5) {
      return 'Waiting Quoting';
    }
    if (status === 6) {
      return 'Quoting';
    }
    if (status === 7) {
      return 'Quotes Ready';
    }
    if (status === 17) {
      return 'Pending Accept/Reject';
    }
    if (status === 20) {
      return 'Preprocessing';
    }
    if (status === 30) {
      return 'Processing';
    }
    if (status === 40) {
      return 'PostProcessing';
    }
    if (status === 100) {
      return 'Finished';
    }
    if (status === 110 || status === 120) {
      return 'Downloaded';
    }
    return 'Waiting';
  },
  languages: {
    auto: 'Auto',
    en: 'English',
    es: 'Spanish',
    el: 'Greek',
    fa: 'Persian',
    de: 'Deutsch',
    nl: 'Dutch',
    ja: 'Japanese',
    pt: 'Portuguese Brazil',
    pp: 'Portuguese Portugal',
    sv: 'Swedish',
    zh: 'Chinese',
    fr: 'French',
    it: 'Italian',
    pl: 'Polish',
    ru: 'Russian',
    va: 'Valencian',
    ar: 'Arabic',
    ca: 'Catalan',
    ko: 'Korean',
    sl: 'Slovenian',
    sk: 'Slovak',
  },

  getStatusByText: status => {
    const aux = {
      processing_error: 'Processing Error',
      waiting: 'Waiting',
      waiting_quoting: 'Waiting Quoting',
      quoting: 'Quoting',
      quote_ready: 'Quotes Ready',
      pending_accept_reject: 'Pending Accept/Reject',
      preprocessing: 'Pre processing',
      processing: 'Processing',
      post_processing: 'Post Processing',
      finished: 'Finished',
      downloaded: 'Downloaded',
    };
    return aux[status];
  },
};

export default functions;
